.header {
    background-color: $white;
    border-bottom: .1rem solid $text;
    margin-bottom: 7.2rem;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 12rem;
    z-index: 3;

    .container {
        display: flex;
        justify-content: center;
        align-items: center;
        height: 100%;
        padding: 0 27rem;
        position: relative;
    }

    &-logo {
        display: block;
        width: 26.9rem;
        position: absolute;
        top: 3.7rem;
        left: 0;

        img {
            width: 100%;
        }
    }

    &-nav {
        height: 100%;
    }

    &-menu {
        display: flex;
        font-size: 1.6rem;
        line-height: 1.06;
        height: 100%;
        text-transform: uppercase;
        position: relative;

        li {
            display: flex;
            align-items: center;
            padding: 0 2.2rem;

            &.active {
                font-family: $font-family-base-medium;
                position: relative;

                &:before {
                    background: url(../images/bg-menu.png) 0 0 no-repeat;
                    content: '';
                    display: block;
                    width: 3.2rem;
                    height: 1.4rem;
                    position: absolute;
                    bottom: -.1rem;
                    left: 50%;
                    transform: translateX(-50%)
                }
            }
        }

        a {
            color: $text;
        }
    }

    .btn {
        width: 16rem;
        text-transform: uppercase;
        position: absolute;
        top: 3rem;
        right: 0;
    }
}