.services {
    &-page {
        padding: 0 1.5rem 8rem;

        .section-services {
            padding: 0 0 8rem;
            margin: 0 0 5.6rem;

            &:before,
            &:after {
                display: none;
            }

            &_right {
                padding-left: 10rem;

                .wish-one {
                    margin-left: 2.6rem;
                }

                .or-row {
                    margin-left: 3.5rem;
                }

                .wish-two {
                    margin-left: 3.7rem;
                }
            }
        }
    }

    &-advantages {
        h2 {
            margin: 0 auto 3rem;
            max-width: 55rem;
            text-align: center;
        }

        .btn {
            width: 40rem;
            margin: 0 auto;
        }

        &_one {
            color: rgba($text, .7);
            height: 100%;
            max-width: 34.3rem;
            padding-top: 1rem;
            position: relative;

            .icon-number {
                height: 6rem;
                margin-bottom: 3.8rem;
                padding-left: 3.5rem;
                position: relative;

                .number {
                    color: rgba($bg, .9);
                    font-family: $font-family-base-bold;
                    font-size: 96px;
                    line-height: 24px;
                    position: absolute;
                    top: 1.2rem;
                    left: 0;
                }

                .icon {
                    position: relative;
                }
            }

            .subtitle {
                color: $text;
                font-family: $font-family-base-medium;
                line-height: 1.125;
                margin-bottom: 2.4rem;
            }

            .text {
                p {
                    margin-bottom: 2rem;
                }
            }

            li {
                padding: 0 0 2rem 2.3rem;
                position: relative;

                &:before {
                    background: url(../images/angle.svg) 0 0 no-repeat;
                    background-size: cover;
                    content: '';
                    display: block;
                    width: .7rem;
                    height: 1.2rem;
                    position: absolute;
                    top: .7rem;
                    left: 0;
                }
            }

            &.line {
                padding-left: 3rem;

                &:before {
                    background-color: #eaeaea;
                    content: '';
                    display: block;
                    width: .1rem;
                    height: 100%;
                    position: absolute;
                    top: 0;
                    left: -1.5rem;
                }

                &:after {
                    background: url(../images/vline.png) 0 0 no-repeat;
                    content: '';
                    display: block;
                    width: 1.4rem;
                    height: 3rem;
                    position: absolute;
                    top: 50%;
                    left: -1.5rem;
                    transform: translateY(-50%)
                }

                &-left {
                    &:before {
                        background-color: #eaeaea;
                        content: '';
                        display: block;
                        width: .1rem;
                        height: 100%;
                        position: absolute;
                        top: 0;
                        right: -.75rem;
                    }

                    &:after {
                        background: url(../images/vline.png) 0 0 no-repeat;
                        content: '';
                        display: block;
                        width: 1.4rem;
                        height: 3rem;
                        position: absolute;
                        top: 50%;
                        right: -.75rem;
                        transform: translateY(-50%) rotate(180deg)
                    }
                }
            }

            &.flex {
                display: flex;
                max-width: none;
                padding-left: 0;

                &.line {
                    &:before,
                    &:after {
                        left: -.75rem;
                    }
                }

                .text {
                    max-width: 40rem;
                }
            }
        }
    }

    &-tab {
        margin-bottom: 3.2rem;
    }
}