.iziModal {
    background: none !important;
    box-shadow: none !important;
    max-width: none !important;
    height: 100% !important;

    .iziModal {
        &-wrap {
            background: url(../images/bg-blue.svg) 0 0 no-repeat;
            height: 100% !important;
        }

        &-content {
            display: flex;
            justify-content: flex-end;
            height: 100%;
            position: relative;
        }
    }
}

.modal {
    &-body {
        color: $white;
        width: 60.5rem;
        padding: 16.8rem 23.5rem 5rem 0;
    }

    &-close {
        background: url(../images/modal-close.svg) 0 0 no-repeat;
        background-size: cover;
        border: none;
        border-radius: 0;
        width: 9.1rem;
        height: 5.8rem;
        position: absolute;
        top: 7.7rem;
        right: 13.4rem;
    }

    &-title {
        font-family: $font-family-base-medium;
        font-size: 2.4rem;
        line-height: 1.08;
        margin-bottom: 5rem;
        text-align: center;
    }

    &-content {
        padding-bottom: 5rem;

        .form-item {
            input,
            textarea {
                background-color: rgba($white,.7);
                border-color: transparent;
            }

            &_checkbox {
                border-top: none;
                padding-top: 0;

                label {
                    color: $white;
                    max-width: 22.5rem;

                    &:before {
                        background-color: transparent;
                        border-color: $white;
                        top: .8rem;
                    }
                }

                a {
                    color: $white;
                    text-decoration: underline;
                }
            }

            &.error {
                .error-text {
                    display: inline-block;
                }

                input,
                textarea {
                    border-color: $red;
                }
            }
        }
    }
}