.estimate {
    &-page {
        padding: 0 2.7rem 8rem;

        @media screen and (max-width: $smartphone-middle) {
            padding: 0 1.7rem 4.7rem;
        }
    }

    &-top {
        display: flex;
        justify-content: space-between;
        margin-bottom: 6.3rem;

        &_caption {
            font-size: 1.8rem;
            line-height: 1.33;
            max-width: 32rem;
        }

        &_item {
            border-left: .1rem solid $border;
            padding: .5rem 0 2rem 6.7rem;
            position: relative;

            &:before {
                background: url(../images/vline2.png) 0 0 no-repeat;
                content: '';
                display: block;
                width: 1.5rem;
                height: 2.4rem;
                position: absolute;
                top: 50%;
                left: -.1rem;
                transform: translateY(-50%)
            }

            &-in {
                display: flex;
                align-items: center;
                padding-left: 2.7rem;
                position: relative;
            }

            .number {
                color: rgba($bg, .9);
                font-family: $font-family-base-bold;
                font-size: 9.6rem;
                line-height: 0.25;
                position: absolute;
                top: 1.3rem;
                left: 0;
            }

            span[class*="icon-"] {
                margin-right: 2.1rem;
                position: relative;
            }


            &.one {


                p {
                    max-width: 14.2rem;
                }

            }

            &.two {
                p {
                    max-width: 28rem;
                }
            }
        }
    }

    &-form {
        div[class*="col-"] {
            position: relative;
        }

        .btn {
            width: 24rem;
            height: 6rem;
            margin: 0 auto;
            padding: 1rem 3rem;
        }

        @media screen and (max-width: $tablet) {
            .form-item {
                margin: 0 auto;
            }
        }

        @media screen and (max-width: $smartphone-middle) {
            width: 100%;
        }
    }

    &-ou {
        color: $blue;
        font-family: $font-family-base-medium;
        font-size: 1.6rem;
        line-height: 1.13;
        position: absolute;
        bottom: 3.9rem;
        right: -.9rem;

        @media screen and (max-width: $tablet) {
            position: relative;
            bottom: auto;
            right: auto;
            display: block;
            margin-bottom: 1.5rem;
            text-align: center;
        }
    }

    &-end {
        max-width: 40rem;
        margin: 5rem auto 7rem;
        text-align: center;

        h3 {
            margin-bottom: 3.2rem;
        }
    }
}