.portfolio {
    &-page {
        padding-bottom: 4.4rem;

        &_one {
            h1 {
                max-width: 50rem;
                margin-bottom: 1.6rem;
            }
        }
    }

    &-top {
        line-height: 1.63;
        width: 85rem;
        margin: 0 auto 15.8rem;
        text-align: center;

        &_icon {
            display: flex;
            justify-content: center;
            height: 5.7rem;
            margin-bottom: 2rem;
        }

        &_open {
            .text {
                max-width: 7.5rem;
                margin: 0 auto;
            }
        }

        &_release {
            .text {
                max-width: 10rem;
                margin: 0 auto;
            }
        }

        &_city {
            .text {
                max-width: 33.5rem;
                margin: 0 auto;
            }
        }
    }

    &-one {
        background-color: $white;
        box-shadow: .2rem .2rem 1rem rgba($black, .1);
        border-radius: 1.6rem;
        font-family: $font-family-base-medium;
        margin-bottom: 4rem;
        position: relative;

        .counter {
            background: url(../images/bg-about.png) 0 0 no-repeat;
            color: rgba($white,.6);
            display: block;
            font-size: 1.8rem;
            line-height: 1.11;
            width: 16.5rem;
            height: 14.5rem;
            position: absolute;
            top: -1.7rem;
            right: -1.7rem;

            span {
                position: absolute;
                top: 4.5rem;
                right: 4.6rem;
            }
        }

        &_pic {
            width: 100%;
            height: 36rem;

            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }

        &_name {
            display: flex;
            align-items: center;
            height: 9.4rem;
            line-height: 1.125;
            padding: 1rem 4rem;
            text-align: right;
        }
    }

    &-slider {
        &-big {
            margin: 7.1rem 0 1.7rem;
            position: relative;

            .owl {
                &-stage-outer {
                    overflow: hidden;
                }

                &-nav {
                    button.owl {
                        &-prev,
                        &-next {
                            position: absolute;
                            top: 50%;
                            left: 0;
                            transform: translateY(-50%) skewX(-22deg);
                        }

                        &-next {
                            left: auto;
                            right: 0;
                        }
                    }
                }
            }
        }

        &-thumb {
            margin-bottom: 8rem;

            .carousel-pic {
                border-radius: 1.6rem;
                position: relative;
                height: 27.4rem;
                width: 34rem;
                overflow: hidden;
                opacity: .2;
                transform: skewX(-22deg);

                img {
                    width: auto;
                    height: 100%;
                    position: absolute;
                    top: 0;
                    left: 50%;
                    transform: translateX(-50%) skewX(22deg);
                }
            }

            .active {
                .carousel-pic {
                    opacity: 1;
                }
            }

            .current {
                .carousel-pic {
                    border: .2rem solid $green;
                }
            }
        }
    }

    &-big {
        width: 97rem;
        height: 60rem;
        border-radius: 1.6rem;
        margin: 0 auto;
        overflow: hidden;

        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

}

.back-link {
    line-height: 2;
    margin-bottom: 2.4rem;

    a {
        color: $blue;
        text-decoration: underline;
    }
}