.page {
    background-color: $white;
    box-shadow: 0 .4rem 2rem rgba($light-blue, .4);
    border-radius: 1.6rem;
    overflow: hidden;
    padding-top: 19.2rem;
    position: relative;
    z-index: 3;

    &:before {
        background: url(../images/bg-page.svg) 0 0 no-repeat;
        background-size: cover;
        content: '';
        display: block;
        width: 100%;
        height: 38.6rem;
        position: absolute;
        top: 10.6rem;
        left: 0;
        z-index: -1;
    }

    &-index {
        &:before {
            background: url(../images/bg-blue.svg) 0 0 no-repeat;
            background-size: cover;
            content: '';
            display: block;
            width: 100%;
            height: 100%;
            position: absolute;
            top: 0;
            right: 0;
        }

        &:after {
            background: url(../images/bg-green.svg) 0 100% no-repeat;
            background-size: cover;
            content: '';
            display: block;
            width: 100%;
            height: 65%;
            position: absolute;
            bottom: 0;
            left: 0;
        }

        .page {
            padding-top: 0;

            &:before {
                display: none;
            }
        }

        .header {
            background-color: transparent;
            border-bottom: none;
        }
    }

    &-about {
        &:before {
            background: url(../images/bg-blue.svg) 0 0 no-repeat;
            background-size: cover;
            content: '';
            display: block;
            width: 100%;
            height: 100%;
            position: absolute;
            top: 0;
            right: 0;
        }
    }
}