.index {
    &-main {
        background: url(../images/bg-welcome.jpg) 50% 50% no-repeat;
        background-size: cover;
        color: $white;
        height: calc(100vh - 4.8rem);
        min-height: 60rem;
        display: flex;
        justify-content: center;
        align-items: center;
        overflow: hidden;
        position: relative;

        &:before {
            background: url(../images/bg-header.png) 0 0 no-repeat;
            content: '';
            display: block;
            width: 15.5rem;
            height: 13.2rem;
            position: absolute;
            bottom: 0;
            left: 0;
        }

        &_welcome {
            font-weight: 500;
            font-size: 16px;
            line-height: 17px;
            width: 53.1rem;
            position: relative;
            text-align: center;
        }

        p {
            max-width: 48.1rem;
            margin:0 auto 3.4rem;
        }

        .btn {
            height: 7.2rem;
            line-height: 7.2rem;
            margin: 0 auto;
            width: 31.6rem;
        }
    }
}