.about {
    &-main {
        margin-bottom: 10.6rem;
    }

    &-pic {
        border-radius: 1.6rem 0 0 1.6rem;
        flex: 0 0 37rem;
        width: 37rem;
        overflow: hidden;

        img {
            width: 100%;
        }
    }

    &-content {
        flex-grow: 2;
        border-radius: 0 1.6rem 1.6rem 0;
        display: flex;
        flex-direction: column;
        justify-content: center;
        padding: 2rem 10rem;
        position: relative;

        .corner {
            background: url(../images/bg-about.png) 0 0 no-repeat;
            width: 16.5rem;
            height: 14.5rem;
            position: absolute;
            top: -1.7rem;
            right: -1.7rem;

            a {
                position: absolute;
                top: 4.3rem;
                right: 4.7rem;

                img {
                    width: 2rem;
                    height: 1.6rem;
                }
            }
        }
    }

    &-one {
        background-color: $white;
        box-shadow: .2rem .2rem 1rem rgba($black, .1);
        border-radius: 1.6rem;
        color: rgba($text, .7);
        display: flex;
        margin-bottom: 2.4rem;

        &.first {
            .about-content {
                .corner {
                    background-image: url(../images/bg-about2.png);
                }
            }
        }
    }

    &-name {
        color: $text;
        font-family: $font-family-base-medium;
        font-size: 2.4rem;
        line-height: 1.08;
        margin-bottom: 1.6rem;
    }

    &-job {
        color: $blue;
        font-family: $font-family-base-medium;
        font-size: 1.8rem;
        line-height: 1.11;
        margin-bottom: 3.2rem;
        max-width: 26.5rem;
    }

    &-map {
        height: 62.6rem;
        margin: -4.5rem 0 -1.7rem;

        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }
}