.section {
    &-services {
        margin: 2rem 0 10.1rem;
        padding: 9.5rem 1.5rem;
        position: relative;

        &:before {
            background-color: rgba(245, 243, 239, 0.3);
            border-radius: 1.6rem;
            content: '';
            display: block;
            width: 150rem;
            height: 100%;
            transform: skew(27deg);
            position: absolute;
            top: 0;
            left: -58.6rem;
            z-index: 1;
        }

        &:after {
            background-color: rgba(245, 243, 239, 0.3);
            border-radius: 1.6rem;
            content: '';
            display: block;
            width: 150rem;
            height: 100%;
            transform: skew(27deg);
            position: absolute;
            top: 0;
            right: -65.4rem;
            z-index: 1;
        }

        .container {
            position: relative;
            z-index: 3;
        }

        &_left {
            font-size: 16px;
            line-height: 1.5;
            padding-right: 10rem;

            p {
                font-size: 2.2rem;
                line-height: 1.45;
                margin-bottom: 3.2rem;
            }

            .text {
                color: rgba($text, .7);
                margin-bottom: 4.8rem;
                max-width: 47.6rem;
            }

            .btn {
                width: 32.8rem;
            }
        }

        &_right {
            padding: 1.3rem 0 0 4.2rem;

            .caption {
                font-family: $font-family-base-medium;
                font-size: 2.2rem;
                line-height: 1.09;
                margin-bottom: 8rem;
            }

            .wish {
                &-one {
                    display: flex;
                    margin: 0 0 4.7rem 8.6rem;
                    position: relative;

                    .number {
                        color: rgba(245, 243, 239, 0.9);
                        font-family: $font-family-base-bold;
                        font-size: 9.6rem;
                        line-height: .25;
                        position: absolute;
                        top: 1.6rem;
                        left: -2.5rem;
                    }

                    .icon-hands {
                        margin-right: 2.1rem;
                        position: relative;
                    }

                    .text {
                        max-width: 14.2rem;
                        position: relative;
                    }
                }

                &-two {
                    display: flex;
                    margin-left: 17.4rem;
                    position: relative;

                    .number {
                        color: rgba(245, 243, 239, 0.9);
                        font-family: $font-family-base-bold;
                        font-size: 9.6rem;
                        line-height: .25;
                        position: absolute;
                        top: .7rem;
                        left: -4.7rem;
                    }

                    .icon-schedule {
                        margin-right: 2.1rem;
                        position: relative;
                    }

                    .text {
                        max-width: 26.4rem;
                        position: relative;
                    }
                }
            }

            .or-row {
                color: #EFECE7;
                font-family: $font-family-base-medium;
                font-size: 2.2rem;
                line-height: 1.09;
                margin: 0 0 4.1rem 9.5rem;
                position: relative;

                &:after {
                    background-color: #f5f3ef;
                    content: '';
                    display: block;
                    width: 100%;
                    height: .1rem;
                    position: absolute;
                    left: 0;
                    top: 1.2rem;
                    z-index: 1;
                }

                span {
                    background-color: #fcfcfb;
                    display: inline-block;
                    padding-right: 1.5rem;
                    position: relative;
                    z-index: 3;
                }
            }
        }

        h2 {
            margin-bottom: 4.8rem;
        }
    }

    &-carousel {
        margin-bottom: 9.4rem;
        display: flex;
        position: relative;

        .owl-nav {
            position: absolute;
            top: 0;
            right: -18rem;
            height: 100%;
            direction: ltr;

            &:before {
                background-color: $white;
                content: '';
                display: block;
                width: 70rem;
                height: 100%;
                position: absolute;
                top: 0;
                left: -4.3rem;
                transform: skew(-22deg);
            }
        }

        &_left {
            flex: 0 0 73%;
            width: 73%;
            padding-right: 3rem;
        }

        &_right {
            background-color: $white;
            flex: 0 0 27%;
            width: 27%;
            position: relative;
        }

        &_more {
            text-align: right;
            position: absolute;
            left: -1.3rem;
            bottom: 0;
            width: 16.5rem;
            z-index: 5;

            .number {
                color: $text;
                font-family: $font-family-base-medium;
                font-size: 3.6rem;
                line-height: .89;
            }

            a {
                color: $blue;
                font-size: 1.6rem;
                line-height: 3.2rem;
                text-decoration: underline;
            }
        }

        .carousel-pic {
            border-radius: 1.6rem;
            position: relative;
            height: 27.4rem;
            width: 34rem;
            overflow: hidden;
            transform: skewX(-22deg);

            img {
                width: auto;
                height: 100%;
                position: absolute;
                top: 0;
                left: 50%;
                transform: translateX(-50%) skewX(22deg);
            }
        }
    }

    &-expertise {
        padding-bottom: 8rem;
        font-size: 1.6rem;
        line-height: 1.5;

        &_top {
            display: flex;
            justify-content: space-between;
        }

        h2 {
            margin-bottom: 4.8rem;
        }

        &_content {
            color: rgba($text, 0.7);
            max-width: 50rem;

            .caption {
                color: $text;
                margin-top: 3.2rem
            }
        }

        &_pic {
            width: 100%;
            max-width: 60.7rem;
            margin-bottom: 1.7rem;

            img {
                width: 100%;
            }
        }

        .btn {
            width: 28.3rem;
            position: relative;
            z-index: 2;
        }

        &_list {
            text-align: center;
            padding: 4.3rem 12rem 8rem 4rem;
            position: relative;
            z-index: 1;

            &:before {
                background-color: #FCFBFA;
                border-radius: 1.6rem;
                content: '';
                display: block;
                width: 95%;
                height: calc(100% + 2.5rem);
                position: absolute;
                top: -3.5rem;
                left: -7rem;
            }

            &:after {
                background-color: #FCFBFA;
                border-radius: 1.6rem;
                content: '';
                display: block;
                width: 25rem;
                height: calc(100% + 2.5rem);
                position: absolute;
                top: -3.5rem;
                right: 6.2rem;
                transform: skewX(-25.5deg)
            }

            .list {
                &-item {
                    margin: 0 auto;
                    position: relative;
                    z-index: 2;
                }

                &-icon {
                    display: block;
                    height: 7.2rem;
                    margin: 0 auto;
                }

                &-first {
                    max-width: 22rem;

                    .list-icon {
                        width: 5rem;

                        img {
                            width: 5rem;
                            height: 5.1rem;
                        }
                    }
                }

                &-second {
                    max-width: 17.2rem;

                    .list-icon {
                        width: 6.6rem;

                        img {
                            width: 6.6rem;
                            height: 4.7rem;
                        }
                    }
                }

                &-third {
                    max-width: 15.6rem;

                    .list-icon {
                        width: 6rem;

                        img {
                            width: 6rem;
                            height: 6rem;
                        }
                    }
                }
            }
        }
    }

    &-contact {
        padding: 0 1.5rem;

        h2 {
            position: relative;
        }

        .container {
            padding: 6.9rem 0 6.2rem;
            position: relative;

            &:before {
                background-color: #FCFBFA;
                border-radius: 1.6rem;
                content: '';
                display: block;
                width: 95%;
                height: 100%;
                position: absolute;
                top: 0;
                left: -7rem;
            }

            &:after {
                background-color: #FCFBFA;
                border-radius: 1.6rem;
                content: '';
                display: block;
                width: 25rem;
                height: 100%;
                position: absolute;
                top: 0;
                right: 6.2rem;
                transform: skewX(-25.5deg)
            }
        }

        h2 {
            margin-bottom: 6rem;
        }
    }

    &-estimate {
        background-color: $white;
        box-shadow: .2rem .2rem 1rem rgba($black, 0.1);
        border-radius: 1.6rem;
        max-width: 101.3rem;
        margin: 0 auto;
        padding: 5.6rem 9rem 4.2rem;

        h3 {
            font-family: $font-family-base-medium;
            font-size: 2.4rem;
            line-height: 1.08;
            margin-bottom: 5.6rem;
            text-align: center;
        }

        @media screen and (max-width: $hd - 100) {
            padding-left: 2.7rem;
            padding-right: 2.7rem;
        }

        @media screen and (max-width: $smartphone-middle) {
            background-color: transparent;
            box-shadow: none;
            border-radius: 0;
            padding-left: 0;
            padding-right: 0;
        }
    }
}