.btn {
    background: none;
    border-radius: .8rem;
    color: $white;
    display: block;
    font-family: 'Gotham HTF Medium';
    font-size: 1.6rem;
    line-height: 1.125;
    padding: 2.7rem 3rem;
    height: 5.8rem;
    transition: all .3s ease-out;
    text-align: center;
    position: relative;

    &:hover,
    &:focus,
    &:active,
    &.focus {
        outline: none !important;
        box-shadow: none;
        text-decoration: none;
    }

    &:disabled,
    &.disabled {
        opacity: .5;
    }

    @media screen and (max-width: $smartphone-middle) {
        width: 100%;
    }

    &-gradient {
        background: #84d2b1;
        background: -moz-linear-gradient(left,  #84d2b1 0%, #34ad73 100%);
        background: -webkit-linear-gradient(left,  #84d2b1 0%,#34ad73 100%);
        background: linear-gradient(to right,  #84d2b1 0%,#34ad73 100%);
        filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#84d2b1', endColorstr='#34ad73',GradientType=1 );
        height: 7.2rem;
    }

    &-blue {
        background-color: $text;
    }

    &-left {
        span {
            position: relative;
        }

        &:before {
            background-color: $text;
            border-radius: .8rem;
            content: '';
            display: block;
            width: 6rem;
            height: 100%;
            transform: skew(-17deg);
            position: absolute;
            top: 0;
            left: -.7rem;
        }
    }

    &-right {
        span {
            position: relative;
        }

        &:before {
            background: -moz-linear-gradient(left,  rgba(62,178,123,0) 0%, rgba(60,177,121,1) 22%, rgba(52,173,115,1) 100%);
            background: -webkit-linear-gradient(left,  rgba(62,178,123,0) 0%,rgba(60,177,121,1) 22%,rgba(52,173,115,1) 100%);
            background: linear-gradient(to right,  rgba(62,178,123,0) 0%,rgba(60,177,121,1) 22%,rgba(52,173,115,1) 100%);
            filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#003eb27b', endColorstr='#34ad73',GradientType=1 );
            border-radius: 0 1.4rem .8rem 0;
            content: '';
            display: block;
            width: 6rem;
            height: 100%;
            transform: skew(-29deg);
            position: absolute;
            top: 0;
            right: -1.7rem;
        }
    }
}

a.btn {
    line-height: 5.8rem;
    padding: 0 3rem;

    &-gradient {
        line-height: 7.2rem;
    }
}
